import axios from './axios'

export function toType(obj) {
  return ({}).toString.call(obj).match(/\s([a-zA-Z]+)/)[1].toLowerCase()
}
export function filterNull(o) {
  for (var key in o) {
    if (o[key] === undefined || o[key] === null || o[key] === '') {
      delete o[key]
    }
    if (toType(o[key]) === 'string') {
      o[key] = o[key].trim()
    } else if (toType(o[key]) === 'object') {
      o[key] = filterNull(o[key])
    } else if (toType(o[key]) === 'array') {
      o[key] = filterNull(o[key])
    }
  }
  return o
}

export function serializeParam(obj, prefix) {
  let str = []
  let p
  for (p in obj) {
    if (obj.hasOwnProperty(p)) {
      let k = prefix ? prefix + "[" + p + "]" : p
      let v = obj[p]
      str.push((v !== null && typeof v === "object") ? serializeParam(v, k) : k + "=" + v)
    }
  }
  return str.join("&");
}
export function httpGet(url, paramsData) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: paramsData
    }).then((res) => {
      resolve(res.data)
    }).catch((err) => {
      const res = err.response
      if (res) {
        reject(err)
      }
    })
  })
}

export function httpPost(url, paramsData) {
  return new Promise((resolve, reject) => {
    const param = filterNull(paramsData)
    axios.post(url, param, {
      emulateJSON: true
    }).then((res) => {
      resolve(res.data)
    }).catch((err) => {
      const res = err.response
      if (res) {
        reject(err)
      }
    })
  })
}

export function httpPostJson(url, paramsData) {
  return new Promise((resolve, reject) => {
    let urlPath = url + '?'
    let idx = 0
    const param = filterNull(paramsData)
    const strParam = serializeParam(param)
    // for (const key of Object.keys(param)) {
    //   urlPath += idx === 0 ? `${key}=${param[key]}` : `&${key}=${param[key]}`
    //   idx++
    // }
    axios.post(urlPath + strParam).then((res) => {
      resolve(res.data)
    }).catch((err) => {
      const cuo = err.response
      if (cuo) {
        reject(cuo)
      }
    })
  })
}



