<template>
  <div id="app" :loading="isLoading">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      isLoading: true
    }
  },
  created() {
    window.onload = function() {
      this.isLoading = false
    }
  }
}
</script>

<style>
html,body{
  height:100%;
  width:100%;
  margin:0;
  padding:0
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height:100%;
  width:100%;
}
</style>



